.hidden {
  display: none !important;
}

.slider-inner {
  will-change: scroll-position;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  scrollbar-width: none;
  gap: 0 1rem;
  display: flex;
  position: relative;
  overflow: scroll hidden;
}

.slider-inner::-webkit-scrollbar {
  display: none;
}

.slider-item {
  scroll-snap-align: start;
  flex: 0 0 100%;
  position: relative;
}

@media screen and (width >= 576px) {
  .slider-item {
    flex: 0 0 50%;
  }
}

@media screen and (width >= 768px) {
  .slider-item {
    flex: 0 0 33%;
  }
}

@media screen and (width >= 992px) {
  .slider-item {
    flex: 0 0 25%;
  }
}

.slider-button {
  margin: 0;
  padding: 0;
}

.slider-button-svg {
  width: 1.5rem;
  transition: all .2s ease-in-out;
}

@media screen and (width >= 768px) {
  .slider-button-svg {
    width: 2rem;
  }
}

.slider-button-svg-path {
  fill: #373737;
  transition: all .2s ease-in-out;
}

.slider-button:disabled {
  opacity: .85;
  pointer-events: none;
}

.slider-button:disabled .slider-button-svg-path {
  opacity: .6;
  cursor: initial;
}

.slider-pager {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: 1rem;
  display: flex;
}

.slider-pager-item {
  cursor: pointer;
  background-color: #d3d3d3;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: .25rem;
  padding: .5rem;
  transition: all .2s ease-in-out;
  display: flex;
  position: relative;
}

.slider-pager-item:focus, .slider-pager-item:hover {
  background-color: #686868;
}

.slider-pager-item.active {
  background-color: #000 !important;
}

.slider-pager-item span {
  pointer-events: none;
  display: none;
  position: absolute;
}

.slider-button-flex {
  position: relative;
}

.slider-button-flex .slider-nav {
  z-index: 1;
  pointer-events: none;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.slider-button-flex .slider-button {
  pointer-events: initial;
  cursor: pointer;
  background: none;
  border: none;
  height: 100%;
}

.slider-button-flex .slider-inner {
  margin: 0 1.8rem;
}

@media screen and (width >= 768px) {
  .slider-button-flex .slider-inner {
    margin: 0 3rem;
  }
}

.slider-item-img-3x2, .slider-item-img-4x3, .slider-item-img-16x9 {
  object-fit: cover;
  object-position: center;
  width: 100%;
}

.slider-item-img-3x2 {
  aspect-ratio: 3 / 2;
}

.slider-item-img-4x3 {
  aspect-ratio: 4 / 3;
}

.slider-item-img-16x9 {
  aspect-ratio: 16 / 9;
}
